html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-clr-primary);
}

@media (min-width: 40em) {
  body:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 15.625rem;
    background-color: var(--background-top);
    z-index: -1;
  }
}

fieldset {
  display: inline-block;
  border: none;
  margin: 0;
  padding: 0;
}

section {
  margin-bottom: 3.125rem;
}

ol,
ul {
  list-style: none;
  padding: 0;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

h1 {
  color: var(--text-clr-secondary);
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 64em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text-clr-secondary);
}

p {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(329, 70%, 58%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(37, 97%, 70%);
  --youtube: hsl(348, 97%, 39%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-toggle-bg-start: hsl(210, 78%, 56%);
  --dark-toggle-bg-end: hsl(146, 68%, 55%);
  --dark-toggle-button: hsl(228, 28%, 20%);
  --dark-text-primary: hsl(228, 34%, 66%);
  --dark-text-secondary: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-toggle-bg: hsl(230, 22%, 74%);
  --light-toggle-button: hsl(228, 46%, 96%);
  --light-text-primary: hsl(230, 12%, 44%);
  --light-text-secondary: hsl(230, 17%, 14%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --background-top: var(--dark-top-bg);
    --text-clr-primary: var(--dark-text-primary);
    --text-clr-secondary: var(--dark-text-secondary);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle-bg: linear-gradient(
      225deg,
      var(--dark-toggle-bg-start) 0%,
      var(--dark-toggle-bg-end) 98.02%
    );
    --toggle-button: var(--dark-toggle-button);
  }
}
body.light {
  --background: var(--light-bg);
  --background-top: var(--light-top-bg);
  --text-clr-primary: var(--light-text-primary);
  --text-clr-secondary: var(--light-text-secondary);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle-bg: var(--light-toggle-bg);
  --toggle-button: var(--light-toggle-button);
}

body.dark {
  --background: var(--dark-bg);
  --background-top: var(--dark-top-bg);
  --text-clr-primary: var(--dark-text-primary);
  --text-clr-secondary: var(--dark-text-secondary);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle-bg: linear-gradient(
    225deg,
    var(--dark-toggle-bg-start) 0%,
    var(--dark-toggle-bg-end) 98.02%
  );
  --toggle-button: var(--dark-toggle-button);
}

.container {
  width: min(68.75rem, 100% - 3.125rem);
  margin: 0 auto;
}

.grid-parent {
  display: grid;
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .grid-parent {
    grid-auto-flow: column;
  }
}

.flex-parent {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 40em) {
  .flex-parent {
    justify-content: flex-end;
    gap: 0.75rem;
  }
}

.even-columns {
  display: grid;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .even-columns {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
  }
}
@media (min-width: 64em) {
  .even-columns {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto;
  }
}

.card {
  position: relative;
  display: grid;
  overflow: hidden;
  text-align: center;
  gap: 1.875rem;
  padding: 1.625rem 0;
  border-radius: 0.3125rem;
  background-color: var(--card-bg);
  transition: all 150ms linear;
}
.card:is(:hover, :focus-visible) {
  background-color: var(--card-hover);
  cursor: pointer;
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--text-clr-primary);
}
.card__count, .card__count--sm {
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1;
  letter-spacing: -0.125rem;
  margin-bottom: 0.25rem;
  color: var(--text-clr-secondary);
}
.card__count--sm {
  font-size: 2rem;
}
.card__title {
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
  color: var(--text-clr-primary);
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--positive {
  color: var(--limegreen);
}
.card__change--positive::before {
  content: url(../../../images/icon-up.svg);
  font-size: 0.25rem;
  vertical-align: middle;
  margin-right: 0.25rem;
}
.card__change--negative {
  color: var(--brightred);
}
.card__change--negative::before {
  content: url(../../../images/icon-down.svg);
  font-size: 0.25rem;
  vertical-align: middle;
  margin-right: 0.25rem;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card--sm {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  grid-template-rows: repeat(2, auto);
  justify-content: space-between;
  justify-items: start;
  text-align: center;
  row-gap: 1.5rem;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  background-color: var(--card-bg);
  transition: all 150ms linear;
}
.card--sm > *:nth-child(even) {
  align-self: center;
  justify-self: end;
}
.card--sm:is(:hover, :focus-visible) {
  cursor: pointer;
  background-color: var(--card-hover);
}

.toggle__wrapper {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  text-align: center;
  width: 3em;
  height: 1.5em;
  border-radius: 1.5em;
  cursor: pointer;
}
.toggle input[type=radio] {
  display: inline-block;
  position: relative;
  z-index: 1;
  width: 45%;
  height: 100%;
  opacity: 0;
  margin: 0;
  cursor: pointer;
}
.toggle__bg {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  border-radius: 2.625em;
  transition: all 0.1s ease-out;
  background: var(--toggle-bg);
}
.toggle__switcher {
  display: block;
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background: var(--toggle-button);
  transition: all 0.15s ease-out;
  z-index: 2;
}
#radio-light:checked ~ .toggle__switcher {
  left: 55%;
}

.header {
  margin: 1.875rem 0;
}
@media (min-width: 40em) {
  .header {
    margin: 2.5rem 0;
  }
}
.header__content {
  padding-bottom: 0.875rem;
  border-bottom: 0.0625rem solid var(--toggle-bg-light);
}
@media (min-width: 40em) {
  .header__content {
    padding: 0;
    border: none;
  }
}
.header__toggle span {
  font-weight: 700;
  color: var(--text-clr-primary);
}